<template>
  <div class="bg-carousel">
    <b-carousel
      id="carousel1"
      style="text-shadow: 1px 1px 2px #333;"
      controls
      indicators
      background="#ababab"
      :interval="5000"
      img-width="1024"
      img-height="480"
    >
      <b-carousel-slide v-for="slide in slides" :key="slide.key" class="carousel-item">
        <img ref="carImg" slot="img" :style="margins" class="d-block img-fill" :src="slide.imgUrl" />
        <h1>{{slide.imgTitle}}</h1>
        <p>{{slide.imgDescription}}</p>
      </b-carousel-slide>
    </b-carousel>
  </div>
</template>
<script>
export default {
  props: {
    slides: {
      type: Array
    }
  },
  data() {
    return {
      margins: {
        marginTop: "",
        marginLeft: ""
      }
    };
  },
  mounted() {
    this.calcMargins();
    const eventHandler = () => this.calcMargins();
    window.addEventListener("resize", eventHandler);
    this.$on(`hook:destroyed`, () => {
      window.removeEventListener("resize", eventHandler);
    });
  },
  methods: {
    calcMargins() {
      const imgRect = this.$refs.carImg.getBoundingClientRect();

      this.margins.marginTop = "-" + imgRect.height / 2 + "px";
      this.margins.marginLeft = "-" + imgRect.width / 2 + "px";
      console.log(this.margins);
    }
  }
};
</script>
<style>
.img-fill {
  min-width: 100% !important;
  min-height: 100% !important;
  width: auto;
  height: auto;
  position: absolute;
  left: 50%;
  top: 50%;
}
</style>